module.exports = "import * as React from 'react'\nimport { TodoListView } from './TodoListView'\n\nexport interface TodoListProps {\n\n}\n\nexport interface TodoListState {\n  items: string[]\n}\n\nexport class TodoList extends React.PureComponent<TodoListProps, TodoListState> {\n  state = { items: [] }\n\n  handleAdd = (item: string) => {\n    this.setState({\n      items: [...this.state.items, item ]\n    })\n  }\n\n  handleDelete = (index: number) => {\n    const next = this.state.items.slice()\n    next.splice(index, 1)\n\n    this.setState({\n      items: next\n    })\n  }\n\n  handleEdit = (index: number, value: string) => {\n    const next = this.state.items.slice()\n    next[index] = value\n\n    this.setState({\n      items: this.state.items\n    })\n  }\n\n  render() {\n    return (\n      <TodoListView\n        items={this.state.items}\n        onAdd={this.handleAdd}\n        onEdit={this.handleEdit}\n        onDelete={this.handleDelete}\n      />\n    )\n  }\n}\n"


